@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Fascinate&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: Roboto,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.wave {animation:wave .5s ease-in-out 1;}
.swirl-in-fwd{animation:swirl-in-fwd .25s ease-out both}

.wave-infinite{animation:wave 1.5s ease-in-out infinite}

.roll-in-left{-webkit-animation:roll-in-left .6s ease-out both;animation:roll-in-left .6s ease-out both}

.header{
  padding:1rem;
  margin: 2rem .5rem 0rem .5rem;

  position:relative;
  left:50%;
  transform: translateX(-50%) rotateZ(-1deg);
  width:90%;

  display:grid !important;
  grid-template-rows: auto auto;
  box-shadow: .5rem .5rem .5rem  rgb(0, 52, 131);
  background-color: rgb(0, 102, 255);
  color: white;

  font-family: 'Source Sans Pro', sans-serif;
  font-size:5vw;
}


.title{
  font-family: Fascinate, sans-serif;
  text-align: center;
}

.sub-title{
  text-transform: uppercase;
  font-family: "Sans Source Pro", sans-serif;
  font-size:1.85vw;
  text-align: center;
}

.sub-title-char{
  margin:0 .1rem;
  padding: 0 .25rem;

  background-color: rgb(140, 0, 255);
  color: white;
}
